#projectsTable {
  width: 100%;
}

.paginate_button {
  height: 18px;
  padding: 0.5rem 0.75rem;
}

.sorting::after {
    content: '\21C5' !important;
}

.sorting_desc::after {
    content: '\21CA' !important;
}


.sorting_asc::after {
    content: '\21C8' !important;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

table {
  background-color: transparent;

  col[class*="col-"] {
    position: static;
    display: table-column;
    float: none;
  }

  td[class*="col-"], th[class*="col-"] {
    position: static;
    display: table-cell;
    float: none;
  }
}

caption {
  padding-top: 8px;
  padding-bottom: 8px;
  color: #777777;
  text-align: left;
}

th {
  text-align: left;
}

.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;

  > {
    thead > tr > th, tbody > tr > th, tfoot > tr > th, thead > tr > td, tbody > tr > td, tfoot > tr > td {
      padding: 8px;
      line-height: 1.42857143;
      vertical-align: top;
    }

    thead > tr > th {
      vertical-align: bottom;
      border-bottom: 2px solid #dddddd;
    }

    caption + thead > tr:first-child > th, colgroup + thead > tr:first-child > th, thead:first-child > tr:first-child > th, caption + thead > tr:first-child > td, colgroup + thead > tr:first-child > td, thead:first-child > tr:first-child > td {
      border-top: 0;
    }

    // tbody + tbody {
    //   border-top: 2px solid #dddddd;
    // }
  }

  .table {
    background-color: #ffffff;
  }
}

.table-striped > tbody > tr:nth-of-type(odd) {
  background-color: #f9f9f9;
}

